.appeal {
    width: 100%;
    padding-top: 40px;
    padding-bottom: 20px;

    font-family: $roboto;
    color: $darktext;
    line-height: normal;
}

.appeal__title {
    margin-bottom: 24px;
}

.appeal__button {
    @include btn-theme();
    max-width: 240px;

    padding: 9px 20px;
    margin: 0 auto 16px;
}

.appeal__phone {
    margin: 0;
    
    font-size: 14px;
    text-align: center;
}

@media #{$tablet} {
    .appeal__title {
        .title {
            font-size: 20px;
        }
    }
}
@media #{$desktop} {
    .appeal {
        padding-top: 80px;
        padding-bottom: 40px;
    }

    .appeal__title {
        margin-bottom: 32px;

        .title {
            font-size: 38px;
        }
    }

    .appeal__button {
        max-width: 448px;
        padding: 18px 36px;
        margin-bottom: 32px;

        border-radius: 38px;

        font-size: 32px;
    }

    .appeal__phone {
        font-size: 26px;
    }
}