.top-triggers {
    font-family: $roboto;
    color: $darktext;
    line-height: normal;
}

.top-triggers__title {
    @include visually-hidden;
}

.top-triggers__list {
    padding: 0;
    margin: 0;
    list-style: none;
}

.top-triggers__item {
    margin-bottom: 36px;

    display: flex;
    align-items: center;

    &:last-child {
        margin-bottom: 0;
    }
}

.top-triggers__image {
    position: relative;
    flex: 0 0 64px;
    width: 64px;
    height: 64px;
    margin-right: 18px;

    svg {
        @include svg-icon
    }
}

.top-triggers__item-title {
    display: block;
    margin-bottom: 2px;

    font-size: 18px;
    font-weight: 700;
}

.top-triggers__item-text {
    display: block;
    
    font-size: 14px;
}

@media #{$tablet} {
    .top-triggers {
        color: $lighttext;
    }
}

@media #{$tablet} {
    .top-triggers__item {
        margin-bottom: 72px;
    }

    .top-triggers__image {
        flex-basis: 120px;
        width: 120px;
        height: 120px;
        margin-right: 40px;
    }

    .top-triggers__item-title {
        margin-bottom: 5px;

        font-size: 34px;
    }

    .top-triggers__item-text {
        font-size: 26px;
    }
}