.testimonials {
    width: 100%;
    padding-top: 40px;
    padding-bottom: 40px;

    font-family: $roboto;
    color: $darktext;
    line-height: normal;
}

.testimonials__title {
    margin-bottom: 24px;
}

.testimonials__slider {
    .slick-arrow {
        position: absolute;
        width: 17px;
        height: 24px;
        padding: 0;

        background-color: transparent;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        border: none;

        font-size: 0;
        top: 50%;
        transform: translateY(-50%);
    }

    .slick-prev {
        left: -10px;

        background-image: url('../images/slick-arrow-left.svg');
    }

    .slick-next {
        right: -10px;

        background-image: url('../images/slick-arrow-right.svg');
    }
}

.testimonials__slide {
    padding: 0 10px;
}

.testimonials__slide-inner {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.testimonials__column {
    width: 48%;

    &--with-border {
        border: 1px solid $themecolor;
        border-radius: 2px;
    }
}

.testimonials__slide-image {
    position: relative;
    
    img {
        position: relative;
        width: 100%;
        max-width: 160px;
        height: auto;
        // min-height: 250px;
        max-height: auto;

        top: 0;
        left: 50%;
        transform: translateX(-50%);

        border-radius: 2px 2px 0 0;
    }
}

.testimonials__slide-author {
    padding: 8px;

    font-size: 12px;
    text-align: center;
}

.testimonials__slide-title {
    margin-bottom: 8px;

    font-size: 14px;
    font-weight: 700;
}

.testimonials__slide-text {
    font-size: 12px;
    font-weight: 400;

    p {
        margin: 0;
        margin-bottom: 4px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

@media #{$tablet} {
    .testimonials__slide {
        padding: 0 20px;
    }
}

@media #{$desktop} {
    .testimonials {
        padding-top: 80px;
        padding-bottom: 80px;
    }

    .testimonials__title {
        margin-bottom: 48px;
    }

    .testimonials__slider {
        .slick-arrow {
            width: 34px;
            height: 45px;
            z-index: 1;
        }
    }

    .testimonials__slide {
        padding: 0 37px;
    }

    .testimonials__column {
        flex: 1 1 auto;

        &--with-border {
            width: 240px;
            max-width: 240px;
            margin-right: 34px;

            border-radius: 6px
        }
    }

    .testimonials__slide-image {
        img {
            max-width: none;
            border-radius: 6px 6px 0 0;
        }
    }

    .testimonials__slide-author {
        padding: 16px;

        font-size: 22px;
    }

    .testimonials__slide-title {
        margin-bottom: 16px;

        font-size: 26px;
    }

    .testimonials__slide-text {
        font-size: 22px;

        p {
            margin-bottom: 7px;
        }
    }
}
