.problems {
    width: 100%;
    padding-top: 40px;
    padding-bottom: 40px;

    font-family: $roboto;
    color: $darktext;
    line-height: normal;
}

.problems__title {
    margin-bottom: 24px;
}

.problems__list {
    list-style: none;
    padding: 0 10px;
    margin: 0;
}

.problems__col {
    margin-bottom: 20px;

    &:last-child {
        margin-bottom: 0;
    }
}

.problems__item {
    position: relative;
    margin-bottom: 20px;

    display: flex;
    align-items: center;

    &:last-child {
        margin-bottom: 0;
    }
}

.problems__image {
    position: relative;
    width: 30px;
    height: 30px;
    flex: 0 0 30px;
    margin-right: 16px;

    svg {
        @include svg-icon;
    }
}

.problems__content {
    width: 100%;
    flex: 1 1 auto;
}

.problems__item-text {
    display: block;

    font-size: 14px;
}

.problems__additional-text {
    display: block;
    font-size: 10px;
}

@media #{$tablet} {
    .problems__list-wrapper {
        display: flex;
        flex-flow: row nowrap;
    }

    .problems__col {
        width: 50%;
        margin: 0;

        display: flex;
        justify-content: center;
    }

    .problems__list {
        width: auto;
        margin: 0 auto;
        
        display: flex;
        flex-flow: column nowrap;
    }

    .problems__item {
        &:first-child {
            margin-top: auto;
        }

        &:last-child {
            margin-bottom: auto;
        }
    }
}

@media #{$desktop} {
    .problems {
        padding-top: 80px;
        padding-bottom: 80px;
    }

    .problems__title {
        margin-bottom: 48px;
    }

    .problems__list {
        padding: 0 35px;
    }

    .problems__item {
        margin-bottom: 40px;
    }

    .problems__image {
        width: 56px;
        height: 56px;
        flex: 0 0 56px;
        margin-right: 32px;

        svg {
            @include svg-icon;
        }
    }

    .problems__item-text {
        font-size: 26px;
    }

    .problems__additional-text {
        font-size: 18px;
    }
}