.advantages {
    width: 100%;
    padding-top: 40px;
    padding-bottom: 20px;

    font-family: $roboto;
    color: $darktext;
    line-height: normal;

    background-color: #727075;
    background-image: url('../images/advantages/background_s.jpg');
    background-size: cover;
    background-position: center;
}

.advantages__title {
    margin-bottom: 24px;
}

.advantages__list {
    margin: 0 0 80px;
    padding: 0 10px;
    list-style: none;
}

.advantages__item {
    position: relative;
    margin-bottom: 24px;
    padding-left: 35px;

    font-size: 16px;
    color: $lighttext;
    font-weight: 400;

    &:last-child {
        margin-bottom: 0;
    }

    &:before {
        content: '';
        position: absolute;
        width: 19px;
        height: 19px;
        top: 50%;
        left: 0;
        transform: translateY(-50%);

        background-image: url('../images/advantages/check.svg');
        background-size: cover;
        background-position: center;
        border-radius: 50%;

        box-shadow: 0px 2px 7px rgba(230, 95, 37, 0.5);
    }
}

@media #{$tablet} {
    .advantages {
        background-image: url('../images/advantages/background_m.jpg');
    }

    .advantages__list {
        padding-left: 34px;
    }
}

@media #{$desktop} {
    .advantages {
        padding-top: 80px;
        padding-bottom: 40px;

        background-image: url('../images/advantages/background_l.jpg');
    }

    .advantages__title {
        margin-bottom: 48px;
    }

    .advantages__list {
        padding-left: 40px;
    }

    .advantages__item {
        padding-left: 68px;
        margin-bottom: 48px;

        font-size: 32px;

        &::before {
            width: 36px;
            height: 36px;
        }
    }
}
