.services {
    width: 100%;
    padding-top: 40px;
    padding-bottom: 40px;

    font-family: $roboto;
    color: $darktext;
    line-height: normal;
}

.services__title {
    margin-bottom: 36px;
}

.services__list-wrapper {
    margin-bottom: 36px;
}

.services__col {
    margin-bottom: 24px;

    &:last-child {
        margin-bottom: 0;
    }
}

.services__list {
    padding: 0 10px;
    margin: 0;
    list-style: none;
}

.services__item {
    position: relative;
    padding-left: 22px;
    margin-bottom: 24px;

    &:last-child {
        margin-bottom: 0;
    }

    &::before {
        content: '';
        position: absolute;
        width: 10px;
        height: 10px;
        left: 0;
        top: 7px;

        border-radius: 50%;
        background-color: $lightthemecolor;
        box-shadow: 0px 4px 4px rgba(240, 171, 82, 0.3);
    }
}

.services__name {
    font-size: 20px;
    font-weight: 700;
}

.services__price {
    font-size: 16px;
    font-weight: 400;
}

.services__button {
    @include btn-theme();
    max-width: 240px;
    padding: 10px 20px;
    margin: 0 auto;
}

@media #{$tablet} {
    .services__list-wrapper {
        display: flex;
        flex-flow: row nowrap;
    }

    .services__col {
        width: 50%;
        margin: 0;
    }

    .services__list {
        padding: 0 33px;

        display: flex;
        flex-flow: column nowrap;
    }
}

@media #{$desktop} {
    .services {
        padding-top: 80px;
        padding-bottom: 80px;
    }

    .services__title {
        margin-bottom: 72px;
    }

    .services__list-wrapper {
        margin-bottom: 72px;
    }

    .services__list {
        padding: 0 54px;
    }

    .services__item {
        margin-bottom: 48px;
        padding-left: 43px;

        &::before {
            width: 19px;
            height: 19px;

            top: 15px;
        }
    }

    .services__name {
        margin-bottom: 5px;

        font-size: 38px;
    }

    .services__price {
        font-size: 32px;
    }

    .services__button {
        max-width: 448px;
        padding: 18px 36px;

        border-radius: 38px;

        font-size: 32px;
    }
}