.main-header {
    font-family: $roboto;
    color: $lighttext;
    line-height: normal;
}

.main-header__row {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
}

.main-header__logo {
    width: 100%;
    margin: 36px auto 0; 

    img {
        @include img;
        width: 179px;
        margin: 0 auto;
    }
}

.main-header__info {
    max-width: 150px;
    margin: 0;

    text-align: left;
    font-size: 10px;
}

.main-header__phone {
    font-size: 12px;
    text-align: right;
}

.main-header__phone-number {
    display: none;
    position: relative;

    color: $lighttext;
    font-size: 18px;
    font-weight: 700;

    transition: color 300ms ease;

    &:hover,
    &:active,
    &:focus {
        text-decoration: none;
        color: darken($lighttext, 15%);
    }

    &--mobile {
        display: block;
    }
}

.main-header__phone-time {
    font-weight: 700;
}

@media #{$tablet} {
    .main-header__row {
        flex-wrap: nowrap;
    }

    .main-header__logo {
        order: -1;
        width: 100px;
        margin: 0;
        margin-right: 30px;

        img {
            width: 100%;
        }
    }

    .main-header__info {
        max-width: 250px;
        margin: 0 auto;

        text-align: center;
        font-size: 12px;
    }

    .main-header__phone-number {
        display: block;

        &--mobile {
            display: none;
        };
    }
}

@media #{$desktop} {
    .main-header__logo {
        width: 186px;
    }

    .main-header__info {
        max-width: 490px;

        font-size: 22px;
    }

    .main-header__phone {
        font-size: 22px;
    }

    .main-header__phone-number {
        font-size: 34px;
    }
}